import React, { useState } from 'react'
import Contact from './Contact';
import {ListTown} from './ListTown'

function MyMap (){
    const [SelectedTown, setSelectedTown] =useState('Boulogne-Sur-Mer')

    function handleClickTop (){
        window.scrollTo(0, 0)
    }

    function handleClick (e){
        setSelectedTown(e.target.dataset.name)
        const MyMap = document.getElementById("MyMap");
        const height = MyMap.offsetTop + MyMap.offsetHeight
        window.scrollTo(0, height)
    }

    return (
        <div className="container mt-5">
            <p id="test">Choisissez une carte :</p>
            <div className="ListTown">
                {ListTown.map((val, key) => 
                    val.town === SelectedTown ? <div data-name={val.town} className="town-btn selectedTown" href="#" onClick={handleClick}>{val.town}</div>  :
                     <div data-name={val.town} className="town-btn" href="#" onClick={handleClick}>{val.town}</div> 
               
            )}
            </div>
            <h1>Carte {SelectedTown !==null ? SelectedTown : 'Boulogne-Sur-Mer'}</h1>
            <Contact />
            
            {/* blabla */}
            <div >
                {ListTown.filter(val => val.town === SelectedTown).map(val => 
                <div id="MyMap">
                    <iframe frameborder="0" allowfullscreen src={"https:" +val.sourceTown + "?scaleControl=false&miniMap=false&scrollWheelZoom=true&zoomControl=true&allowEdit=false&moreControl=true&searchControl=null&tilelayersControl=null&embedControl=false&datalayersControl=true&onLoadPanel=databrowser&captionBar=false&fullscreenControl=false&locateControl=false&editinosmControl=false"}></iframe>
                </div>
                    
                )}
                
            </div>
            <div id="Top" onClick={handleClickTop}>
                <i class="fas fa-arrow-circle-up fa-3x"></i>
            </div>
        </div>
    );
}

export default MyMap