import React from 'react'
import { Link, Switch, Route, Redirect } from 'react-router-dom';
import MyMap from './MyMap';
import MentionsLegales from './MentionsLegales';

function Navbar () {
    return (
        <div>
            <header>
                  <nav className="navbar navbar-expand-lg navbar-light bg-light">
                    <div className="container-fluid">
                        <Link className="navbar-brand" to="/">Les commerces du boulonnais</Link>
                        {/* <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                        </button> */}
                        <div className="collapse navbar-collapse" id="navbarNav">
                        {/* <div class="navbar-nav">
                            <Link class="nav-link active" aria-current="page" to="/other-map">Test</Link>
                        </div> */}
                        </div>
                    </div>
                </nav>
            </header>
          
            <Switch>
                <Route path="/mentions-legales" component={MentionsLegales} />
                <Route path="/" component={MyMap} />
            </Switch>
            <footer>
        <div class="container text-center">
           <p><a href="https://www.nicolas-sueur.fr/">Réalisé par Nicolas - Création de Sites Web</a></p>
           <p><Link to="/mentions-legales">Mentions Légales</Link></p>
        </div>
      </footer>  
        </div>
    );
}

export default Navbar